<script>
  import Login from './Login.svelte';
  import Addresses from './Addresses.svelte';
  import ChatMessage from './ChatMessage.svelte';
  import { onMount } from 'svelte';
  import { afterUpdate } from 'svelte';
  import { channelId } from './stores.js';
  import { username, user } from './user';
  import debounce from 'lodash.debounce';
  import { Form, FormGroup, FormText, Input, InputGroup, InputGroupText, Label, Col, Container, Row, Button } from 'sveltestrap';
  import { Icon, Styles } from 'sveltestrap';

  import GUN from 'gun';
  const db = GUN();

  let newMessage;
  let messages = [];
  let channel = 'chatlobby';
  var curr;

  let scrollBottom;
  let lastScrollTop;
  let canAutoScroll = true;
  let unreadMessages = false;

  let showAccountBook  = false;

  const unsubscribe = channelId.subscribe(value => {
    messages = [];
    channel = value;
    curr = db.get(channel);
    curr.map().once(function(node){
      log(node)
    });
  });

  function autoScroll() {
    setTimeout(() => scrollBottom?.scrollIntoView({ behavior: 'auto' }), 50);
    unreadMessages = false;
  }

  function watchScroll(e) {
    canAutoScroll = (e.target.scrollTop || Infinity) > lastScrollTop;
    lastScrollTop = e.target.scrollTop;
  }

  $: debouncedWatchScroll = debounce(watchScroll, 1000);

  onMount(() => {
    // var match = {
    //   // lexical queries are kind of like a limited RegEx or Glob.
    //   '.': {
    //     // property selector
    //     '>': new Date(+new Date() - 1 * 1000 * 60 * 60 * 3).toISOString(), // find any indexed property larger ~3 hours ago
    //   },
    //   '-': 1, // filter in reverse
    // };
    //
    // // Get Messages
    // db.get(channel)
    //   .map(match)
    //   .once(async (data, id) => {
    //     if (data) {
    //       // Key for end-to-end encryption
    //       const key = '#foo';
    //
    //       var message = {
    //         // transform the data
    //         who: await db.user(data).get('alias'), // a user might lie who they are! So let the user system detect whose data it is.
    //         what: (await SEA.decrypt(data.what, key)) + '', // force decrypt as text.
    //         when: GUN.state.is(data, 'what'), // get the internal timestamp for the what property.
    //       };
    //
    //       if (message.what) {
    //         messages = [...messages.slice(-100), message].sort((a, b) => a.when - b.when);
    //         if (canAutoScroll) {
    //           autoScroll();
    //         } else {
    //           unreadMessages = true;
    //         }
    //       }
    //     }
    //   });
  });

  afterUpdate(() => {
  });

  async function log(node) {
    const key = '#foo';
    var message = {
      // transform the data
      who: await db.user(node).get('alias'), // a user might lie who they are! So let the user system detect whose data it is.
      what: (await SEA.decrypt(node.what, key)) + '', // force decrypt as text.
      when: GUN.state.is(node, 'what'), // get the internal timestamp for the what property.
    };
    if (message.what) {
      messages = [...messages.slice(-100), message].sort((a, b) => a.when - b.when);
      if (canAutoScroll) {
        autoScroll();
      } else {
        unreadMessages = true;
      }
    }
  }

  async function sendMessage() {
    const secret = await SEA.encrypt(newMessage, '#foo');
    const message = user.get('all').set({ what: secret });
    const index = new Date().toISOString();
    db.get(channel).get(index).put(message);
    newMessage = '';
    canAutoScroll = true;
    autoScroll();
  }
</script>

<style>
</style>

<div class="container-tile">
  {#if $username}
  {#if showAccountBook}
  <Addresses />
  {/if}
    <main class="parallax" on:scroll={debouncedWatchScroll}>
      {#each messages as message (message.when)}
        <ChatMessage {message} sender={$username} />
      {/each}

      <div bind:this={scrollBottom} />
    </main>

    <form on:submit|preventDefault={sendMessage}>
      <InputGroup>
        <Input  bind:value={newMessage} maxlength="100" />
        <InputGroupText>
        <Button outline light type="submit" disabled={!newMessage}>
          <Icon name="play-fill" />
        </Button>
        </InputGroupText>
      </InputGroup>
    </form>

    {#if !canAutoScroll}
      <div class="scroll-button">
        <Button outline secondary on:click={autoScroll}>
          <Icon name="chat-left-fill" />
          {#if unreadMessages}
          {/if}
        </Button>
      </div>
    {/if}
    {:else}
      <main>
        <Login />
      </main>
    {/if}
</div>
