<script>
  import { username, user } from './user';
  import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    NavLink
  } from 'sveltestrap';
  import { Button } from 'sveltestrap';

  function signout() {
    user.leave();
    username.set('');
  }

  let isOpen = false;
  const toggle = () => (isOpen = !isOpen);

</script>

<Navbar color="dark" dark>
  <NavbarBrand href="/" class="me-auto">
    {#if $username}
    <div class="user-bio">

    <!-- <img src={`https://avatars.dicebear.com/api/identicon/${$username}.svg`} alt="avatar" /> -->
    <span><small>{$username.substring(0, 6)}...{$username.substring($username.length - 4)}</small></span>
    </div>

    <!-- <button class="signout-button" on:click={signout}>Sign Out</button> -->

    {:else}

    {/if}
  </NavbarBrand>
  <NavbarToggler on:click={toggle} class="me-2" />
  <Collapse {isOpen} navbar>
    <Nav navbar>
      <NavItem>
        <!-- <NavLink href="#components/"></NavLink> -->
        <NavLink href="#" on:click={signout}><Button block>Sign Out</Button></NavLink>
      </NavItem>
    </Nav>
  </Collapse>
</Navbar>
