<script>
  import { user } from './user';
  import { onMount, beforeUpdate, afterUpdate } from 'svelte';
  import { defaultChainStore, web3, selectedAccount, connected, chainData } from 'svelte-web3'
  import { Form, FormGroup, FormText, Input, Label, Col, Container, Row, Button, Styles } from 'sveltestrap';

  const enableBrowser = () => defaultChainStore.setBrowserProvider()

  $: checkAccount = $selectedAccount || '0x0000000000000000000000000000000000000000'

  let username;
  let password;

  async function login() {
    await signin();
    user.auth(username, password, ({ err }) => err && alert(err));
  }

  async function signup() {
    await signin();
    user.create(username, password, ({ err }) => {
      if (err) {
        alert(err);
      } else {
        login();
      }
    });
  }

  async function signin() {
    let msg = await $web3.eth.accounts.hashMessage($selectedAccount);
    password = await $web3.eth.sign(msg, $selectedAccount);
  }

  onMount(
    () => {
    enableBrowser();
    }
  )

  afterUpdate(() => {
    username = $selectedAccount
  });

</script>

